var check = require('../../common/utils.js');
export default {
	data() {
		return {
			name: '',
			idcard: '',
			imgs: [], //本地图片地址数组
			picPaths: [], //网络路径
			positive: 'positive',
			reverse: 'reverse',
			pos: '',
			rev: '',
			type: '',
			p_flag: true,
			r_flag: true,
			p_url: '',
			r_url: '',
			shade: true
		};
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	methods: {
		moveHandle: function(e) {
			e.preventDefault();
			e.stopPropagation();
		},
		getName: function(e) {
			this.name = e.detail.value;
		},
		getIdcard: function(e) {
			this.idcard = e.detail.value;
		},
		//添加上传图片
		chooseImageTap: function(e) {
			var that = this;
			var flag = e.currentTarget.dataset.flag;
			uni.showActionSheet({
				itemList: ['从相册中选择', '拍照'],
				itemColor: '#00000',
				success: function(res) {
					if (!res.cancel) {
						if (res.tapIndex == 0) {
							if (flag == 'positive') {
								that.chooseWxImage1('album');
							}
							if (flag == 'reverse') {
								that.chooseWxImage2('album');
							}
						} else if (res.tapIndex == 1) {
							if (flag == 'positive') {
								that.chooseWxImage1('camera');
							}
							if (flag == 'reverse') {
								that.chooseWxImage2('camera');
							}
						}
					}
				}
			});
		},
		// 图片本地路径
		chooseWxImage1: function(type) {
			var that = this;
			var imgsPaths = that.imgs;
			var obj = {};
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: [type],
				success: function(res) {
					for (var i = imgsPaths.length - 1; i >= 0; i--) {
						for (var j in imgsPaths[i]) {
							if (j == 'positive') {
								imgsPaths.splice(i, 1);
							}
						}
					}
					obj.positive = res.tempFilePaths[0];
					that.imgs.push(obj);
					that.p_url = res.tempFilePaths[0];
					that.p_flag = false;
					that.imgs = that.imgs;
				}
			});
		},
		chooseWxImage2: function(type) {
			var that = this;
			var imgsPaths = that.imgs;
			var obj = {};
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: [type],
				success: function(res) {
					for (var i = imgsPaths.length - 1; i >= 0; i--) {
						for (var j in imgsPaths[i]) {
							if (j == 'reverse') {
								imgsPaths.splice(i, 1);
							}
						}
					}
					obj.reverse = res.tempFilePaths[0];
					that.imgs.push(obj);
					(that.r_url = res.tempFilePaths[0]), (that.r_flag = false), (that.imgs = that.imgs);
					that.rev = imgsPaths[1].reverse;
					that.pos = imgsPaths[0].positive;
				}
			});
		},
		//上传服务器
		upImgs: function(imgpaths, index) {
			var that = this;
			let token = this.global_.token;
			for (var key in imgpaths[index]) {
				uni.uploadFile({
					url: this.url + 'realnames/', //上传接口
					//url:'http://192.168.1.218:8005/api/v1.1.0/realname/',
					filePath: imgpaths[index][key],
					name: key,
					header: {
						Authorization: 'JWT' + ' ' + token
					},
					formData: null,
					success: function(res) {
						console.log(res)
						if (res.statusCode == 400) {
							uni.showToast({
								title: '数据异常',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 406) {
							uni.showToast({
								title: '图片太大，请重新上传',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						index++;
						if (index == imgpaths.length) {
							uni.request({
								url: that.url + 'realnames/',
								// url:th'realname/',
								method: 'POST',
								data: {
									name: that.name,
									idcard: that.idcard
								},
								header: {
									Authorization: 'JWT' + ' ' + token
								},
								success: function(res) {
									console.log(res)
									if (res.statusCode == 402) {
										uni.showToast({
											title: '身份证号已存在，请重新认证',
											icon: 'none',
											duration: 2000
										});
										return false;
									}
									if (res.statusCode == 401) {
										uni.showToast({
											title: '身份证号错误',
											icon: 'none'
										});
										return false;
									}
									if (res.statusCode == 410) {
										uni.showToast({
											title: '图片格式错误',
											icon: 'none'
										});
										return false;
									}
									if (res.statusCode == 200) {
										that.shade = true;
									}
								}
							});
						} else {
							that.upImgs(imgpaths, index);
						}
					},
					fail: function(res) {}
				});
			}
		},
		submitt: function() {
			var that = this;
			if (!that.name) {
				uni.showToast({
					title: '用户名不能为空',
					icon: 'none'
				});
				return false;
			}
			if (!that.idcard) {
				uni.showToast({
					title: '身份证号不能为空',
					icon: 'none'
				});
				return false;
			}
			var flag = check.checkIdcard(that.idcard);
			if (!flag) {
				uni.showToast({
					title: '请输入正确的身份证号',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (that.imgs.length !== 2) {
				uni.showToast({
					title: '身份证照不全',
					icon: 'none'
				});
				return false;
			}
			that.upImgs(that.imgs, 0);
		},
		sure: function() {
			uni.redirectTo({
				url: '../../pages/set/set'
			})
		}
	}
};
